import search from '../../common/search.js'
export default {
  data() {
    return {
      searchData: [],
      defaultDate: [],
      searchValue: '',
      item: '',
      searched: false,
      currentIndex: -1,
      indexs: [],
      showMovieInfo: [],
      searchValues: []        
    }
  },
  onShow() {
    this.defaultDate = search.data.rows
    let searchValues
    searchValues = uni.getStorageSync('searchValues')
    if (searchValues.length === 0) {
      searchValues = []
    }
    this.searchValues = searchValues
  },
  methods: {
    search() {
      let searchValue = this.searchValue.replace(/\s*/g,'')
      console.log(searchValue.length);
      if (searchValue === '') {
        uni.showToast({
          title: '无搜索关键词',
          icon: 'none'
        })
        return
      }
      // 数组去重
      let searchValues = this.searchValues
      let found = false
      for (let value in searchValues) {
        if (searchValues[value] === searchValue) {
          found = true
          break
        }
      }
      if (!found) {
        searchValues.unshift(searchValue)
      }
      if (searchValues.length > 9) {
        searchValues = searchValues.slice(0, 10)
        this.searchValues = searchValues
      }
      this.searchValues = searchValues
      uni.setStorageSync('searchValues', searchValues)
      // 视频搜索接口
      this.sendSearch(searchValue)

    },
    // 点击历史搜索词填入输入框
    searchTermsClick(e) {
      this.searchValue = this.searchValues[e.currentTarget.dataset.termsindex]
    },
    recommendMovieClick(e) {
      this.searchValue = e.currentTarget.dataset.moviename
      this.search()
    },
    // 剧情简介的展开与折叠按钮
    toggleShowInfo(e) {
      let gindex = e.currentTarget.dataset.gindex - 0
      if (this.indexs.indexOf(gindex) === -1) {
        this.indexs.push(gindex)
      } else {
        this.indexs = this.indexs.filter((item) => {
          return item != gindex
        });
      }
    },
    // 请求数据
    sendSearch(searchValue) {
      uni.navigateTo({
        url: `../searchpage/searchpage?movieName=${searchValue}`,
        animationType: 'slide-in-bottom'
      })
    },
    clearSearchTerms(){
      let that = this
      uni.showModal({
          title: '提示',
          content: '确认删除搜索历史?',
          success: function (res) {
              if (res.confirm) {
                  that.searchValues = []
                  uni.removeStorageSync('searchValues')
              }
          }
      });
    }
  }
}
